import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { FullMockTestResolver } from '@app/modules/manage/mock-test/reslovers/full-mock-test.reslover';
import { FullMockTestListComponent } from '@app/modules/full-mock-test/full-mock-test-list/full-mock-test-list.component';


const routes: Routes = [
  {
    path: '',
    component: FullMockTestListComponent,
    resolve: {
      fullMockTest: FullMockTestResolver
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
  providers: [
    FullMockTestResolver
  ]
})
export class FullMockTestRoutingModule { }